import React from "react";
import Column from "./Column";

const Table = ({ object }) => {
  const columnContent = Object.keys(object).map((key, i) => {
    console.log(object[key]);
    return <Column content={object[key]} />;
  });

  return <div className="table">{columnContent}</div>;
};

export default Table;



