import React, { Component } from "react";
import Favorites from "./containers/Favorites/Favorites";
import Search from "./containers/Search/Search";
import axios from "axios";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      query: "shopify",
      favorites: {
        0: {
          headerName: "Name",
          content: ["Shopify/Timber", "nozzlegear/ShopifySharp"]
        },
        1: {
          headerName: "Language",
          content: ["Liquid", "TypeScript"]
        },
        2: {
          headerName: "Latest Tag",
          content: ["v1.0", "v1.0.0"]
        },
        3: {
          headerName: "Remove",
          content: ["Remove", "Remove"]
        }
      },
      search: {
        0: {
          headerName: "Name",
          content: ["Shopify/Timber", "nozzlegear/ShopifySharp"]
        },
        1: {
          headerName: "Language",
          content: ["Liquid", "TypeScript"]
        },
        2: {
          headerName: "Latest Tag",
          content: ["v1.0", "v1.0.0"]
        },
        3: {
          headerName: "Add",
          content: ["Add", "Add"]
        }
      }
    };
  }

  componentDidMount() {
    axios
      .get(`https://api.github.com/search/repositories?q=${this.state.query}`)
      .then(json => {
        console.log(json);
        axios
          .get(`${json.data.items[0].url}/releases/latest`)
          .then(release => console.log(release));
      });
  }

  render() {
    return (
      <section className="content">
        <Search object={this.state.search} />
        <Favorites object={this.state.favorites} />
      </section>
    );
  }
}

export default App;



