import React from "react";
import Item from "./Item";

const List = ({ content }) => {
  console.log(`content is, ${content}`);
  let items = content.map((cur, i) => {
    return <Item key={i} item={cur} />;
  });

  return <ul>{items}</ul>;
};

export default List;



